
.home {
    margin-bottom: 10vh;
    margin-top: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 110px;
    height: 40px;
    text-decoration: none;
    color: white;
    font-weight: normal;
    border-radius: 10px;
    overflow: hidden;
    background: linear-gradient(to top left, rgba(111, 220, 255, 0.7), rgba(111, 220, 255, 0.7), rgba(255, 36, 141, 0.7));
}

.home span{
    width: 106px;
    height: 36px;
    display: flex;
    background: linear-gradient(to bottom, #0d192d, #111b38);
    align-items: center;
    justify-content: center;
    position: relative;
    border-radius: 7px;

}

.home span:hover{
    background: linear-gradient(to bottom, #152b4c, #132245);
}

.app-header {
    width: 40%;
    margin-left: 10%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
}
.app-header h2 {
    text-align: left;
    width: 288px;

}

.app-header h3 {
 opacity: 0.8;
}