
@media (min-width:1024px ) and (max-width:1439px ){
    .app-header h2 {
        text-align: left;
        width: 288px;
    }

}

@media (min-width:768px ) and (max-width:1023px ){
    .App {
        background-image: url('./images/Background768px.jpg');
        background-size: cover;
        background-position: center top;
        height: 100vh;
    }
    .app-header {
        width: 100%;
        margin-left: 0;
        align-items: center;
        padding-top: 120px;
    }

    .home {
        position: absolute;
        top: 50px;
        left: 70px;
        margin-left: 0;
        margin-top: 0;

    }

    .sc-dkPtRN button {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        font-weight: bold;
        padding: 13px 35px;
        width: 243px;
        height: 48px;
        color: black;
        text-transform: uppercase;
        background: linear-gradient(90deg, #6FDCFF 0%, #24FF3A 102.52%);
        border-radius: 10px;
    }

    .solanaDesktop {
        display: none;
    }

    .solanaMob {
        display: block;
        height: 90px;
        width: 90px;
        background-position: center center;
        margin: 0;
        margin-bottom: 50px;
    }

    .sc-dkPtRN {
        width: 100%;
        margin-left: 0;
        display: flex;
        justify-content: center;

    }
}
@media (max-width: 767px){
    .App {
        background-image: url('./images/Background768px.jpg');
        background-size: cover;
        background-position: center top;
        height: 100vh;
    }
    .app-header {
        width: 100%;
        margin-left: 0;
        align-items: center;
        padding-top: 60px;
    }

    .app-header h2 {
        font-size: 17px;
        text-align: center;
        width: 210px;
        margin: 0;
        padding: 0;

    }

    .app-header h3 {
        font-size: 14px;
        text-align: center;
    }

    .home {
        position: absolute;
        top: 20px;
        left: 20px;
        margin-left: 0;
        margin-top: 0;
        width: 70px;
        height: 25px;

        border-radius: 5px;

    }

    .home span{
        width: 66px;
        height: 21px;
        border-radius: 4px;
        font-size: 12px;

    }

    .sc-dkPtRN button {
      font-size: 12px;
        width: 200px;
        height: 40px;
    }

    .solanaDesktop {
        display: none;
    }

    .solanaMob {
        display: block;
        height: 60px;
        width: 60px;
        background-position: center center;
        margin: 0;
        margin-bottom: 10px;
    }

    .sc-dkPtRN {
        width: 100%;
        margin-left: 0;
        display: flex;
        justify-content: center;

    }
}