@font-face {
  font-family: 'regular';
  src:url("./fonts/FordAntenna-Regular.ttf");
}
@font-face {
  font-family: 'bold';
  src:url("./fonts/FordAntenna-Bold.ttf");
}
@font-face {
  font-family: 'light';
  src:url("./fonts/FordAntenna-Light.ttf");
}

*{
  font-family: 'regular';
}

.App {
  background-image: url('./images/Background1920px.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center top;
  height: auto;
  width: auto;
  min-height: 100%;
  min-width: 100%;
  position: absolute; 
  top: 0; 
  left: 0; 
  right: 0; 
  bottom: 0; 
}

.sc-dkPtRN {
  width: 40%;
  margin-left: 10%;

}
.sc-dkPtRN button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  padding: 13px 35px;
  width: 243px;
  height: 48px;
  color: black;
  text-transform: uppercase;
  background: linear-gradient(90deg, #6FDCFF 0%, #24FF3A 102.52%);
  border-radius: 10px;
}

.sc-dkPtRN button:hover {
  transition: all linear 0.1s;
  transform: scale(1.02);
  color: #230358;
}

.solana-box {
  width: 15%;
  margin-left: 10%;
  height: 150px;
  margin-top: 10vh;
  background: url("./images/solana1920.png") no-repeat;
  background-size: contain;
  background-position: center top;
}

.solanaMob {
  display: none;
}